import React, {Component} from 'react';
import Todos from "./components/Todos";
import AddTask from "./components/AddTask";
import './App.css';


class App extends Component {
  state = {
    todos: [],
  };
  componentDidMount() {
    fetch("https://my-json-server.typicode.com/NoobLucifer/json-server/to-do")
      .then((data) => data.json())
      .then((res) => { this.setState({todos: res})});
  }
  makeChange = (id) => {
    this.setState({
      todos: this.state.todos.map((todo) => {
        if (todo.id === id) {
          todo.completed = !todo.completed;
        }
        return todo;
      }),
    });
  };
  delTodo = (id) => {
    if (window.confirm("Are you Sure?"))
    fetch(
      `https://my-json-server.typicode.com/NoobLucifer/json-server/to-do${id}`,
      {
        method: "DELETE",
      }
    ).then(() =>
      this.setState({
        todos: [...this.state.todos.filter((todo) => todo.id !== id)],
      })
    );  
  };
  addTodo = (task, description) => {
    fetch("https://my-json-server.typicode.com/NoobLucifer/json-server/to-do", {
      method: "POST",
      body: JSON.stringify({
        task,
        description,
        completed: false,
      }),
      headers: {
        "Content-type": "application/json; charset=UTF-8",
      },
    })
      .then((data) => data.json())
      .then((res) => this.setState({ todos: [...this.state.todos, res] }));
  };
  render() {
    return (
      <div className='App'>
        <AddTask todos={this.state.todos} addTodo={this.addTodo} />
        <hr
          style={{
            border: "none",
            height: "3px",
            margin: "12px 0",
            background:
              " linear-gradient(90deg, rgba(1,1,108,0.9) 0%, rgba(0,153,185,1) 50%, rgba(1,1,108,0.8970938717283788) 100%)",
          }}
        />
        <Todos
          todos={this.state.todos}
          makeChange={this.makeChange}
          delTodo={this.delTodo}
        />
      </div>
    );
  }
}
export default App;
