import React, { Component } from 'react';
import PropTypes from 'prop-types';


class TodoItem extends Component {

    strike = () => {
        return {
            display: 'inline-block',
            textDecoration: this.props.todo.completed ? 'line-through' : 'none',
            margin:'0'
        }
    } 
    forBlockquote = () => {
        if (!this.props.todo.completed && this.props.todo.description !== "") {
          return (
            <blockquote style={{ marginTop: "0px", marginBottom:"0px", color:'#666670'}}>
              {this.props.todo.description}
            </blockquote>
          );
        }
    }   
    render() {
    const { task,id } = this.props.todo;
    return (
      <div
        style={{
          width: "100%",
          margin: "auto",
          padding: "1rem",
          boxSizing: "border-box",
          background: "#20274D",
          color: "#767680",
          borderRadius: "5px",
        }}
      >
        <div style={{ display: "grid", gridTemplateColumns: "6fr .5fr" }}>
          <div>
            <input
              type='checkbox'
              style={{ display: "inline-block", margin: ".3rem",}}
              onChange={this.props.makeChange.bind(this, id)}
            />
            <h3 style={this.strike()}>{task}</h3>
            {this.forBlockquote()}
          </div>
          <div>
            <div
              className='opac'
              style={{
                margin: ".2rem",
                background: "none",
                border: "none",
                color: "rgba(244,189,135,90)",
                cursor:'pointer'
              }}
              onClick={this.props.delTodo.bind(this, id)}
            >
              <i className='fas fa-trash-alt fa-2x'></i>
            </div>
          </div>
        </div>
      </div>
    );
    }
}

TodoItem.propTypes = {
    todo : PropTypes.object.isRequired,
    makeChange: PropTypes.func.isRequired,
    delTodo:PropTypes.func.isRequired,
}

export default TodoItem