import React, { Component } from 'react';
import "../App.css";



class AddTask extends Component {
  state = {
    toggle: false,
    task: "",
    description: "",
  };
  getStyle = {
    display: "inline-block",
    width: "100%",
    border: "none",
    margin: ".5rem 0",
    padding: ".4rem",
    boxShadow: "none",
    color: "#ccc",
    background: "#20274D",
    height: "2.5rem",
    borderRadius: ".5rem",
    boxSizing: "border-box",
  };
  getStyle1 = {
    display: "inline-block",
    width: "20%",
    textAlign: "center",
    border: "none",
    margin: ".5rem 0",
    boxShadow: "none",
    color: "#ccc",
    background: "#023e8a",
    height: "2.5rem",
    borderRadius: ".5rem",
    boxSizing: "border-box",
  };
  onChange = (e) => this.setState({ [e.target.name]: e.target.value });
  onSubmit = (e) => {
    e.preventDefault();
    if(this.state.task !== ''){
        this.props.addTodo(this.state.task , this.state.description);
        this.setState({ toggle: false, task: "", description: "" });
    }else{
        alert('Enter some task!');
    }
  };
  setToggle = () => this.setState({toggle:true});
  render() {
    return (
      <>
        {!this.state.toggle ? (
          <div style={{
              display:'grid',
              gridTemplateColumns:'5fr 1fr',
              gridGap:'.5rem',
              padding:'1rem',
              textAlign:'center'
          }}>
            <div>
                <h1 style={{margin:'0', color:'#ccc'}}>TO-DO LIST</h1>
            </div>
            <div className='add-task' onClick={this.setToggle}>
              <i className='fas fa-plus-circle fa-3x'></i>
            </div>
          </div>
        ) : (
          <form onSubmit={this.onSubmit} style={{ textAlign: "center" }}>
            <input
              type='text'
              name='task'
              style={this.getStyle}
              value={this.state.task}
              onChange={this.onChange}
              placeholder='ADD TODO...'
            />
            <input
              type='text'
              name='description'
              style={this.getStyle}
              value={this.state.description}
              onChange={this.onChange}
              placeholder='ADD Description'
            />
            <input type='submit' className='opac' value='SUBMIT' style={this.getStyle1} />
          </form>
        )}
      </>
    );
  }
}

export default AddTask;
